.App {
  display: flex;
  flex-flow: column nowrap;
}

.App > * {
  margin: auto;
}

.App-header {
  display: flex;
  flex-flow: column nowrap;
}

.App-header > * {
  margin: auto;
}

.App-title {
  font-size: 24px;
}

.App-keyboard {
  display: flex;
  flex-flow: row wrap;
  width: 650px;
}

.App-input {
  font-family: monospace;
  letter-spacing: 1px;
  text-align: center;
  margin: 20px auto;
}

.disabled {
  background-color: darkgray;
}

.App-button {
  width: 50px;
  height: 50px;
  border: 0;
}

button::-moz-focus-inner {
  border: 0;
}

button:active {
  outline: 0;
}

button:hover {
  background-color: bisque;
}

button::-moz-active-inner {
  border: 0;
}
